import React, { Component } from 'react'
import { Table } from 'reactstrap';
import 'bootstrap/dist/css/bootstrap.min.css'

export default class Table1 extends Component {

  btnClicked = () => {
  this.props.isActiveComponent()
}

  render() {
        return (
          <div>
            <h1>List of students data</h1>
            <hr/>
                 <Table dark>
      <thead>
        <tr>
          <th>#</th>
          <th>Full name</th>
          <th>Password</th>
          <th>Hobbies</th>
          <th>Level</th>
          <th>GPA</th>
          <th>Majors</th>
          <th>Remove</th>
        </tr>
      </thead>
              <tbody>
                
                {
                  this.props.users.map((item, index) => {
                    return (
                       <tr key={index}>
                        <th scope="row">{index+1}</th>
                        <td>{item.nameValue}</td>
                        <td>{item.passwordValue}</td>
                        <td>{item.hobbiesValue.join(', ')}</td>
                        <td>{item.levelValue}</td>
                        <td>{item.gpaValue}</td>
                        <td>{item.majorsValue}</td>
                        <td>
                         <svg onClick={()=> this.props.removeUser(index)} style={{cursor:'pointer', marginLeft:'#dc3545'}} xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="red" className="bi bi-dash-square" viewBox="0 0 16 16">
                        <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z"/>
                        <path d="M4 8a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7A.5.5 0 0 1 4 8z"/>
                        </svg>
                        </td>
                        
                       </tr>
                    )
                  })
                }
      </tbody>
            </Table>
            <p style={{textAlign:'end', marginTop:'100px'}}><button onClick={this.btnClicked} className="btn btn-success">Add new user</button></p>
            </div>
        )
    }
}
