import React, { Component } from 'react'
import FormTable from './components/FormTable'
import Table1 from './components/Table1'
//import { Container, Button } from '@material-ui/core'


export default class App extends Component {


  state = {
    users: [],
    isActiveComponent:true
  }
  

  formValues = (values) => {
    let a = this.state.users
    a.push(values)
    this.setState({users:a})
  }

  isActiveComponent = () => {
     this.setState({isActiveComponent:!this.state.isActiveComponent})
  }
  
  removeUser = (index) => {
    let a = this.state.users
    a.splice(index, 1)
    this.setState({users:a})
  }

  render() {

    return (
           <div className="container">
        <div className="row justify-content-center mt-5">
          
          {
            this.state.isActiveComponent?<div className="col-12">
              <Table1 removeUser={this.removeUser} users={this.state.users} isActiveComponent={this.isActiveComponent}/>
            </div> :
              <div className="col-7">
        <FormTable formValues={this.formValues} isActiveComponent={this.isActiveComponent}/></div>
          } 
         </div> 
      </div>
    )}
}
