import React, { Component } from 'react'
import { Button, Form, FormGroup, Input } from 'reactstrap';
import '../main.css'

class FormTable extends Component {
   
  state = {
    nameValue:'',
    passwordValue:'',
    hobbiesValue: [],
    levelValue:'',
    gpaValue:'',
    majorsValue:'',
  }

  formValues = (value, type, isActive) => {
    let newArray = this.state
    

    if (type==='nameValue')
      newArray.nameValue = value
    else if(type==='passwordValue')
      newArray.passwordValue = value
    else if(type==='levelValue')
      newArray.levelValue = value
    else if(type==='gpaValue')
      newArray.gpaValue = value
    else if(type==='majorsValue')
      newArray.majorsValue = value
    else if (type === 'hobbiesValue') {
      if (isActive === true) {
        let a = newArray.hobbiesValue;
        a.push(value)
        newArray.hobbiesValue = a
      } else if(isActive === false) {
        let a = newArray.hobbiesValue;
        let index = a.indexOf(value)
        a.splice(index, 1)
        newArray.hobbiesValue = a
      }
    }

    this.setState(newArray)
    
  }

  formSubmit = (event) => {
    event.preventDefault()
    this.props.formValues(this.state)
    this.props.isActiveComponent()
  }
  

    render() {
        const { nameValue, passwordValue, hobbiesValue, levelValue, gpaValue } = this.state
        
        return (
            <Form className="myForm" onSubmit={this.formSubmit}>
              <h4>Student Data</h4>
              <hr />
              <FormGroup>
                <span className="inputSpan">Name</span>
              <Input className="formInput" type="text" required placeholder="Your name" value={nameValue}
                onChange={(e) => this.formValues(e.target.value, 'nameValue')} />
              <p className="descInput">Plase enter your first name and last name</p>
              </FormGroup>
              
              
              <FormGroup>
                <span className="inputSpan">Password</span>
              <Input  className="formInput" type="password" required placeholder="Your password" value={passwordValue}
                onChange={(e) => this.formValues(e.target.value, 'passwordValue')} />
                 <p className="descInput">Password must be five or more characters</p>
              </FormGroup>

              <FormGroup>
                <span className="inputSpan">Hobbies</span>
              <span className="check">Surfing<Input className="checkInput" type="checkbox"
              value={hobbiesValue} onChange={(e) => this.formValues('Surfing', 'hobbiesValue', e.target.checked)}/></span>
                <span className="check">Running<Input className="checkInput" type="checkbox"
                value={hobbiesValue} onChange={(e) => this.formValues('Running', 'hobbiesValue', e.target.checked)}/></span>
                <span className="check">Biking<Input className="checkInput" type="checkbox"
                value={hobbiesValue} onChange={(e) => this.formValues('Biking', 'hobbiesValue', e.target.checked)}/></span>
                <span className="check">Paddling<Input className="checkInput" type="checkbox"
                value={hobbiesValue} onChange={(e) => this.formValues('Paddling', 'hobbiesValue', e.target.checked)}/></span>
                <p className="descInput">Password must be five or more characters</p>
              </FormGroup>

              <FormGroup>
                <span className="inputSpan">Level</span>
              <span className="check">Freshman<Input className="checkInput" type="radio" name="input1" required
              value={levelValue} onChange={(e) => this.formValues('Freshman', 'levelValue')}/></span>
                <span className="check">Sophomore<Input className="checkInput" type="radio" name="input1" required
                value={levelValue} onChange={(e) => this.formValues('Sophomore', 'levelValue')}/></span>
                <span className="check">Junior<Input className="checkInput" type="radio" name="input1" required
                value={levelValue} onChange={(e) => this.formValues('Junior', 'levelValue')}/></span>
                <span className="check">Senior<Input className="checkInput" type="radio" name="input1" required
                value={levelValue} onChange={(e) => this.formValues('Senior', 'levelValue')}/></span>
                <span className="check">Other<Input className="checkInput" type="radio" name="input1" required
                value={levelValue} onChange={(e) => this.formValues('Other', 'levelValue')}/></span>
                <p className="descInput">Select your grade level</p>
              </FormGroup>
              
              <FormGroup>
                <span className="inputSpan">GPA</span>
                <Input type="select"  className="formInput form-control" required value={gpaValue}
                onChange={(e) => this.formValues(e.target.value, 'gpaValue')}>
                  <option value="">Plase select a value</option>
                  <option value="2">2</option>
                  <option value="2.5">2.5</option>
                  <option value="3">3</option>
                    <option value="3.5">3.5</option>
                    <option value="4">4</option>
                    <option value="4.5">4.5</option>
                </Input>
                <p className="descInput">Select your GPA</p>
              </FormGroup>
              
              <FormGroup>
                <div style={{display:'flex'}}>
                  <span className="inputSpan">Majors</span>
                <Input type="select" className="formInput form-control" required multiple
                onChange={(e) => {this.formValues(e.target.value, 'majorsValue')}}>
                  <option value="Physics">Physics</option>
                  <option value="Chemistry">Chemistry</option>
                  <option value="Mathematics">Mathematics</option>
                  <option value="Biology">Biology</option>
                </Input>
                </div>
                <p className="descInput">Select zero or more majors</p>
              </FormGroup>

              <div className="btnContainer" style={{marginLeft:'100px'}}>
                <Button style={{margin:'5px'}} type="submit" className="btn-info" type="submit">Submit</Button>    
                <Button style={{margin:'5px'}} type="button" onClick={()=>this.props.isActiveComponent()}>Cancel</Button>    
              </div>
                    
            </Form>
        )
    }
}

export default FormTable
